    <div class="patient-info-container">
      <!-- Patient name section -->
      <div class="patient-name">
        <!-- Label for patient name -->
        <h3 class="label">PATIENT</h3>
        <!-- Display patient first and last name -->
        <h3>{{ patientName }} {{ patientLastName }}</h3>
      </div>

      <!-- Breathing rate section -->
      <div class="breathing-rate">
        <!-- Display patient's resting heart rate -->
        <h1>{{ patientBrate }}</h1>
        <!-- Label for resting heart rate -->
        <h3 class="label" style="text-align: left">RESTING <br />HEART RATE</h3>
      </div>

      <!-- Patient information details section -->
      <div class="info">
