<script>
// Import the Vuex store
// import store from '/src/store/index.js';
import { useActiveStudyStore } from '@/src/store/active-study';
import { useAppState } from '@/src/store/ocg-app-state';

// Import the ToolStrip component

// import IconNumber from './IconNumber.vue';
import TaskListOverlay from './TaskListOverlay.vue';

export default {
  // Register the ToolStrip component
  components: { TaskListOverlay },

  // Define props for the component
  props: {
    // Active component indicates the currently displayed component
    activeComponent: { type: String, default: 'analysis' },
  },

  // Component data properties
  data() {
    return {
      // Patient information data
      patient: null,
      patientName: null,
      patientLastName: null,
      patientBrate: null,
      patientage: null,
      patientweight: null,
      patientheight: null,
      patientgender: null,
      // State to track if checkContours is active
      checkContours: null,
      activeStore: useActiveStudyStore(),

      viewTasks: false,
    };
  },

  // Lifecycle hook: mounted

  mounted() {
    const currentQaView = this.activeStore.qualityCheck.viewing;
    if (currentQaView) {
      this.handleclick(currentQaView);
    } else {
      // Initialize checkContours to false
      this.checkContours = false;
    }

    // Get the current patient data from local storage
    // this.patient = JSON.parse(window.localStorage.getItem('currentPatient'));
    this.patient = this.activeStore.activePatient;

    if (!this.patient) {
      this.patientName = 'NOT';
      this.patientLastName = 'PROVIDED';

      this.patientBrate = 'NA';
      this.patientage = 'NA';
      this.patientweight = 'NA';
      this.patientheight = 'NA';
      this.patientgender = 'NA';
    } else {
      // Extract and format patient information
      this.patientName = this.patient.name
        ? this.patient.name.toUpperCase()
        : 'NA';
      this.patientLastName = this.patient.lastname
        ? this.patient.lastname.toUpperCase()
        : 'NA';
      this.patientBrate = this.patient.breathingrate
        ? this.patient.breathingrate
        : 'NA';
      this.patientage = this.patient.age ? this.patient.age : 'NA';
      this.patientweight = this.patient.weight ? this.patient.weight : 'NA';
      this.patientheight = this.patient.height ? this.patient.height : 'NA';
      this.patientgender = this.patient.gender ? this.patient.gender : 'NA';
    }
  },

  // Component methods
  methods: {
    // Handle click events for quality check options
    handleclick(targetView) {
      this.activeStore.qualityCheck.viewing = targetView;
      // Emit the 'updatequalitycheck' event with the checkContours value
      // this.$emit('updatequalitycheck', this.checkContours);
    },

    handleKeyDown() {
      // Placeholder for potential keyboard ShortCut
    },

    logOutUser() {
      const appState = useAppState();
      appState.logOut(); // Logs user out and clears localstorage.
      // Logs user out and clears localstorage.
      // window.localStorage.clear();
      // window.sessionStorage.clear();
      // window.localStorage.setItem('userLoggedIn', 'GUEST USER');
    },
  },
};
</script>

