<!-- 
    SEARCH BAR COMPONENT USED TO FIND PATIENTS GIVEN AN INPUT. 
    CURRENTLY SIMPLY EMITS ITS INPUT TO PARENT.
    COMPONENT PART OF  AREA 19, OCG WEB APP.
    DEVELOPPED IN MONTREAL, QC, CANADA.
    JULY 2023.
  -->

<template>
  <div class="searchbar">
    <!--
    The 'input' element is used to capture the user's search input.
    The 'v-model' directive binds the input value to the 'searchTerm' data property, enabling two-way data binding.
    When the user types in the input field, the 'searchTerm' data property is automatically updated.
    -->
    <input
      type="text"
      v-model="searchTerm"
      @input="handleInput"
      placeholder="Find Patient..."
      aria-labelledby="findPatientSearchBox"
      @keydown="handleKeydown"
    />
  </div>
</template>

<script>
import { usePatientStore } from '@/src/store/patient-data';

export default {
  data() {
    return {
      searchTerm: '',
      patientStore: usePatientStore(),
    };
  },
  created() {
    this.searchTerm = this.patientStore.searchTerm;
  },
  methods: {
    handleInput() {
      // Emit the search term to the parent component
      this.$emit('search-term-updated', this.searchTerm);
    },
    handleSearch() {
      // Emit the search term to the parent component when the search button is clicked
      this.$emit('search-term-updated', this.searchTerm);
    },

    handleKeydown(event) {
      if (event.key === 'Escape') {
        event.preventDefault();
        this.searchTerm = '';
      } else if (event.key === 'Backspace') {
        event.preventDefault();
        this.searchTerm = this.searchTerm.slice(0, -1);
      }
      this.handleInput();
    },
  },
};
</script>

